import HologramsModel from '../models/holograms';

export default async function updateHolograms(hologramsList) {
  const Holograms = HologramsModel();
  await Promise.all(
    hologramsList.map(async (hologram) => {
      return Holograms.findByIdAndUpdate(
        { _id: hologram._id },
        hologram.update
      );
    })
  );

  return { message: 'Hologram(s) updated successfully' };
}
